.about__container{
    /* display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    place-items: center; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1;

}
.about__me{
    width: 30%;
    margin-bottom: 4rem;
    /* aspect-ratio: 1/1; */
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
         transparent, 
         var(--color-primary), 
         transparent
    );
    display: flex ;
    place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    /* height: 100%; */
    overflow: hidden;
    transform: rotate(15deg);
    transition: var(--transition);
 

}
.about__me-image:hover{
    transform: rotate(0) 
}


.about__cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  
    /* grid-template-columns: repeat(3, 1fr); */
    gap: 1.5rem;
}
.about__card {
    background: var(--color-bg-variant);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    border-radius: 1rem;
    width: 25%;
    padding: 2rem;
    text-align: center;
    transition: var(--transistion);
}
.about__card:hover {
    background:transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;

}
.about__icon h5{
    font-size: 1rem;
    
}
.about__icon small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
     padding:1rem 5rem 0.5rem;
     color: var(--color-light);
}


 /* Medium size media query */
 @media screen and (max-width:1024px) {
    
        .about__container{
            
            grid-template-columns: 1fr;
            gap: 0
    
    }
    .about__me{
        width: 50%;
        margin: 2.2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
   
  }
  
  @media screen and (max-width:600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1.1rem;

    }
    .about__content{
        text-align: center;
    }
    .about__content p {
       margin: 1.5rem 0;
    }
  }


